@import "@/theme/index.scss";

.customersBlock {
  width: 100%;
  margin: 2% 4%;
  @include screen-lp {
    margin: 41px 95px 258px 71px;
  }

  &__title {
    @include font-size(500, 24px, 36px);
    padding-bottom: 51px;
  }
  &__userBlock {
    box-shadow: 0px 10px 60px 0px #e2ecf980;
    border-radius: 30px;
    background: white;
    padding: 6% 3%;
    @include screen-lp {
      padding: 30px 44px 40px 38px;
    }
  }
}
